import {
  Card,
  CardContent,
  CardActions,
  Typography,
  Box,
  Chip,
  Avatar,
  IconButton,
  LinearProgress,
  Tooltip,
} from '@mui/material';
import {
  MoreVert,
  TrendingUp,
  TrendingDown,
  Assessment,
  Warning,
  CheckCircle,
} from '@mui/icons-material';
import { Startup } from '@/types';

interface StartupCardProps {
  startup: Startup;
  onView: (startup: Startup) => void;
  onEdit?: (startup: Startup) => void;
  onDelete?: (startup: Startup) => void;
}

export const StartupCard: React.FC<StartupCardProps> = ({
  startup,
  onView,
}) => {
  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'active': return 'success';
      case 'review': return 'warning';
      case 'pending': return 'info';
      case 'rejected': return 'error';
      default: return 'default';
    }
  };

  const getRiskColor = (risk: string) => {
    switch (risk.toLowerCase()) {
      case 'low': return 'success';
      case 'medium': return 'warning';
      case 'high': return 'error';
      default: return 'default';
    }
  };

  const getScoreIcon = (score: number) => {
    if (score >= 80) return <TrendingUp color="success" />;
    if (score >= 60) return <Assessment color="warning" />;
    return <TrendingDown color="error" />;
  };

  return (
    <Card
      sx={{
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        transition: 'all 0.3s ease',
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: 4,
        },
        cursor: 'pointer',
      }}
      onClick={() => onView(startup)}
    >
      <CardContent sx={{ flexGrow: 1 }}>
        {/* Header */}
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <Avatar
            src={startup.logo}
            sx={{ width: 48, height: 48, mr: 2 }}
          >
            {startup.name.charAt(0)}
          </Avatar>
          <Box sx={{ flexGrow: 1 }}>
            <Typography variant="h6" noWrap>
              {startup.name}
            </Typography>
            <Typography variant="body2" color="text.secondary" noWrap>
              {startup.industry}
            </Typography>
          </Box>
          <IconButton
            size="small"
            onClick={(e) => {
              e.stopPropagation();
              // Handle menu click
            }}
          >
            <MoreVert />
          </IconButton>
        </Box>

        {/* Status and Stage */}
        <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
          <Chip
            label={startup.status}
            size="small"
            color={getStatusColor(startup.status)}
            variant="filled"
          />
          <Chip
            label={startup.stage}
            size="small"
            variant="outlined"
          />
        </Box>

        {/* Key Metrics */}
        <Box sx={{ mb: 2 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Funding
            </Typography>
            <Typography variant="body2" fontWeight="500">
              {startup.funding}
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Valuation
            </Typography>
            <Typography variant="body2" fontWeight="500">
              {startup.valuation}
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Founded
            </Typography>
            <Typography variant="body2" fontWeight="500">
              {startup.foundedYear}
            </Typography>
          </Box>
        </Box>

        {/* AI Score */}
        <Box sx={{ mb: 2 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
            <Typography variant="body2" color="text.secondary" sx={{ flexGrow: 1 }}>
              AI Score
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {getScoreIcon(startup.aiScore)}
              <Typography variant="h6" fontWeight="bold" sx={{ ml: 1 }}>
                {startup.aiScore}/100
              </Typography>
            </Box>
          </Box>
          <LinearProgress
            variant="determinate"
            value={startup.aiScore}
            sx={{
              height: 6,
              borderRadius: 3,
              backgroundColor: 'grey.200',
              '& .MuiLinearProgress-bar': {
                borderRadius: 3,
                backgroundColor: startup.aiScore >= 80 ? 'success.main' : 
                                startup.aiScore >= 60 ? 'warning.main' : 'error.main',
              },
            }}
          />
        </Box>

        {/* Risk Assessment */}
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Typography variant="body2" color="text.secondary">
            Risk Level
          </Typography>
          <Chip
            label={startup.riskLevel}
            size="small"
            color={getRiskColor(startup.riskLevel)}
            icon={startup.riskLevel === 'High' ? <Warning /> : <CheckCircle />}
          />
        </Box>
      </CardContent>

      <CardActions sx={{ px: 2, pb: 2 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', width: '100%' }}>
          <Typography variant="caption" color="text.secondary">
            Last updated: {new Date(startup.lastUpdated).toLocaleDateString()}
          </Typography>
          <Tooltip title="Analysis Progress">
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Typography variant="caption" color="text.secondary" sx={{ mr: 1 }}>
                {startup.analysisProgress}%
              </Typography>
              <LinearProgress
                variant="determinate"
                value={startup.analysisProgress}
                sx={{ width: 40, height: 4 }}
              />
            </Box>
          </Tooltip>
        </Box>
      </CardActions>
    </Card>
  );
};
