import { useState, useMemo } from 'react';
import {
  Box,
  Typography,
  ToggleButtonGroup,
  ToggleButton,
  Pagination,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@mui/material';
import { ViewModule, ViewList, GridView } from '@mui/icons-material';
import { StartupCard } from './StartupCard';
import { DataTable } from '@/components/common/DataTable';
import { Startup } from '@/types';

// Mock data
const mockStartups: Startup[] = [
  {
    id: '1',
    name: 'TechFlow Solutions',
    industry: 'SaaS',
    sector: 'Technology',
    stage: 'Series A',
    status: 'active',
    funding: '$2.5M',
    valuation: '$15M',
    foundedYear: 2020,
    aiScore: 85,
    overallScore: 85,
    riskLevel: 'Low',
    lastUpdated: new Date('2024-01-15'),
    analysisProgress: 95,
    recommendation: 'Buy',
    logo: '',
  },
  {
    id: '2',
    name: 'HealthBridge',
    industry: 'HealthTech',
    sector: 'Healthcare',
    stage: 'Seed',
    status: 'review',
    funding: '$1.8M',
    valuation: '$8M',
    foundedYear: 2021,
    aiScore: 72,
    overallScore: 72,
    riskLevel: 'Medium',
    lastUpdated: new Date('2024-01-14'),
    analysisProgress: 60,
    recommendation: 'Hold',
    logo: '',
  },
];

interface StartupGridProps {
  searchQuery: string;
}

type ViewMode = 'grid' | 'list' | 'table';
type SortOption = 'name' | 'aiScore' | 'funding' | 'lastUpdated';

export const StartupGrid: React.FC<StartupGridProps> = ({ searchQuery }) => {
  const [viewMode, setViewMode] = useState<ViewMode>('grid');
  const [sortBy, setSortBy] = useState<SortOption>('aiScore');
  const [page, setPage] = useState(1);
  const itemsPerPage = 12;

  // Filter and sort startups
  const filteredStartups = useMemo(() => {
    let filtered = mockStartups;

    // Apply search filter
    if (searchQuery) {
      filtered = filtered.filter(startup =>
        startup.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        startup.industry.toLowerCase().includes(searchQuery.toLowerCase()) ||
        startup.stage.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    // Apply sorting
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name);
        case 'aiScore':
          return b.aiScore - a.aiScore;
        case 'funding':
          return parseFloat(b.funding?.replace(/[$M]/g, '') || '0') - parseFloat(a.funding?.replace(/[$M]/g, '') || '0');
        case 'lastUpdated':
          return new Date(b.lastUpdated).getTime() - new Date(a.lastUpdated).getTime();
        default:
          return 0;
      }
    });

    return filtered;
  }, [searchQuery, sortBy]);

  // Paginate results
  const paginatedStartups = useMemo(() => {
    const startIndex = (page - 1) * itemsPerPage;
    return filteredStartups.slice(startIndex, startIndex + itemsPerPage);
  }, [filteredStartups, page]);

  const totalPages = Math.ceil(filteredStartups.length / itemsPerPage);

  const handleStartupView = (startup: Startup) => {
    console.log('View startup:', startup);
  };

  const handleStartupEdit = (startup: Startup) => {
    console.log('Edit startup:', startup);
  };

  const handleStartupDelete = (startup: Startup) => {
    console.log('Delete startup:', startup);
  };

  // Table columns for table view
  const tableColumns = [
    { id: 'name', label: 'Company', sortable: true },
    { id: 'industry', label: 'Industry', sortable: true },
    { id: 'stage', label: 'Stage', sortable: false },
    { id: 'funding', label: 'Funding', sortable: true, align: 'right' as const },
    { id: 'aiScore', label: 'AI Score', sortable: true, align: 'center' as const },
    { id: 'status', label: 'Status', sortable: false },
  ];

  return (
    <Box>
      {/* Controls */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h6">
          {filteredStartups.length} Startups
        </Typography>

        <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
          {/* Sort Control */}
          <FormControl size="small" sx={{ minWidth: 120 }}>
            <InputLabel>Sort by</InputLabel>
            <Select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as SortOption)}
              label="Sort by"
            >
              <MenuItem value="aiScore">AI Score</MenuItem>
              <MenuItem value="name">Name</MenuItem>
              <MenuItem value="funding">Funding</MenuItem>
              <MenuItem value="lastUpdated">Last Updated</MenuItem>
            </Select>
          </FormControl>

          {/* View Mode Toggle */}
          <ToggleButtonGroup
            value={viewMode}
            exclusive
            onChange={(_, newMode) => newMode && setViewMode(newMode)}
            size="small"
          >
            <ToggleButton value="grid">
              <GridView />
            </ToggleButton>
            <ToggleButton value="list">
              <ViewModule />
            </ToggleButton>
            <ToggleButton value="table">
              <ViewList />
            </ToggleButton>
          </ToggleButtonGroup>
        </Box>
      </Box>

      {/* Content */}
      {paginatedStartups.length === 0 ? (
        <Box sx={{ textAlign: 'center', py: 8 }}>
          <Typography variant="h6" color="text.secondary">
            No startups found
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Try adjusting your search or filters
          </Typography>
        </Box>
      ) : (
        <>
          {/* Grid View */}
          {viewMode === 'grid' && (
            <Box
              sx={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
                gap: 3,
              }}
            >
              {paginatedStartups.map((startup) => (
                <StartupCard
                  key={startup.id}
                  startup={startup}
                  onView={handleStartupView}
                  onEdit={handleStartupEdit}
                  onDelete={handleStartupDelete}
                />
              ))}
            </Box>
          )}

          {/* List View */}
          {viewMode === 'list' && (
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              {paginatedStartups.map((startup) => (
                <Box key={startup.id} sx={{ maxWidth: 600 }}>
                  <StartupCard
                    startup={startup}
                    onView={handleStartupView}
                    onEdit={handleStartupEdit}
                    onDelete={handleStartupDelete}
                  />
                </Box>
              ))}
            </Box>
          )}

          {/* Table View */}
          {viewMode === 'table' && (
            <DataTable
              columns={tableColumns}
              data={paginatedStartups}
              onRowClick={handleStartupView}
              actions={[
                { label: 'View Details', onClick: handleStartupView },
                { label: 'Edit', onClick: handleStartupEdit },
                { label: 'Delete', onClick: handleStartupDelete },
              ]}
            />
          )}

          {/* Pagination */}
          {totalPages > 1 && (
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
              <Pagination
                count={totalPages}
                page={page}
                onChange={(_, newPage) => setPage(newPage)}
                color="primary"
              />
            </Box>
          )}
        </>
      )}
    </Box>
  );
};
