import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Report } from '@/types';

interface ReportsState {
  generated: Report[];
  templates: ReportTemplate[];
  sharing: SharingState;
  generation: GenerationState;
  loading: boolean;
  error: string | null;
}

interface ReportTemplate {
  id: string;
  name: string;
  description: string;
  sections: string[];
  format: 'pdf' | 'word' | 'powerpoint';
  isDefault: boolean;
}

interface SharingState {
  [reportId: string]: {
    shareUrl?: string;
    accessLevel: 'view' | 'comment' | 'edit';
    expiresAt?: Date;
    password?: string;
  };
}

interface GenerationState {
  [reportId: string]: {
    progress: number;
    stage: string;
    estimatedTime?: number;
  };
}

const initialState: ReportsState = {
  generated: [],
  templates: [],
  sharing: {},
  generation: {},
  loading: false,
  error: null,
};

const reportsSlice = createSlice({
  name: 'reports',
  initialState,
  reducers: {
    setReports: (state, action: PayloadAction<Report[]>) => {
      state.generated = action.payload;
      state.loading = false;
      state.error = null;
    },
    addReport: (state, action: PayloadAction<Report>) => {
      state.generated.push(action.payload);
    },
    updateReport: (state, action: PayloadAction<Report>) => {
      const index = state.generated.findIndex(r => r.id === action.payload.id);
      if (index !== -1) {
        state.generated[index] = action.payload;
      }
    },
    removeReport: (state, action: PayloadAction<string>) => {
      state.generated = state.generated.filter(r => r.id !== action.payload);
      delete state.sharing[action.payload];
      delete state.generation[action.payload];
    },
    setTemplates: (state, action: PayloadAction<ReportTemplate[]>) => {
      state.templates = action.payload;
    },
    updateSharing: (state, action: PayloadAction<{ reportId: string; sharing: SharingState[string] }>) => {
      state.sharing[action.payload.reportId] = action.payload.sharing;
    },
    updateGeneration: (state, action: PayloadAction<{ reportId: string; generation: GenerationState[string] }>) => {
      state.generation[action.payload.reportId] = action.payload.generation;
    },
    clearGeneration: (state, action: PayloadAction<string>) => {
      delete state.generation[action.payload];
    },
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setError: (state, action: PayloadAction<string | null>) => {
      state.error = action.payload;
      state.loading = false;
    },
  },
});

export const {
  setReports,
  addReport,
  updateReport,
  removeReport,
  setTemplates,
  updateSharing,
  updateGeneration,
  clearGeneration,
  setLoading,
  setError,
} = reportsSlice.actions;

export default reportsSlice.reducer;
