import { useState } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Collapse,
  Button,
  Slider,
  FormGroup,
  FormControlLabel,
  Checkbox,
  Chip,
} from '@mui/material';
import { FilterList, Clear, ExpandMore, ExpandLess } from '@mui/icons-material';
import { MultiSelectFilter } from '@/components/common/SearchFilter';

interface FilterPanelProps {
  open: boolean;
  onToggle: () => void;
}

const industryOptions = [
  { value: 'saas', label: 'SaaS' },
  { value: 'fintech', label: 'FinTech' },
  { value: 'healthtech', label: 'HealthTech' },
  { value: 'edtech', label: 'EdTech' },
  { value: 'cleantech', label: 'CleanTech' },
  { value: 'ecommerce', label: 'E-commerce' },
];

const stageOptions = [
  { value: 'pre-seed', label: 'Pre-Seed' },
  { value: 'seed', label: 'Seed' },
  { value: 'series-a', label: 'Series A' },
  { value: 'series-b', label: 'Series B' },
  { value: 'series-c', label: 'Series C+' },
];

const statusOptions = [
  { value: 'active', label: 'Active' },
  { value: 'review', label: 'Under Review' },
  { value: 'pending', label: 'Pending' },
  { value: 'rejected', label: 'Rejected' },
];

export const FilterPanel: React.FC<FilterPanelProps> = ({ open, onToggle }) => {
  const [selectedIndustries, setSelectedIndustries] = useState<string[]>([]);
  const [selectedStages, setSelectedStages] = useState<string[]>([]);
  const [selectedStatuses, setSelectedStatuses] = useState<string[]>([]);
  const [aiScoreRange, setAiScoreRange] = useState<number[]>([0, 100]);
  const [fundingRange, setFundingRange] = useState<number[]>([0, 10]);
  const [riskLevels, setRiskLevels] = useState({
    low: false,
    medium: false,
    high: false,
  });

  const hasActiveFilters = 
    selectedIndustries.length > 0 ||
    selectedStages.length > 0 ||
    selectedStatuses.length > 0 ||
    aiScoreRange[0] > 0 || aiScoreRange[1] < 100 ||
    fundingRange[0] > 0 || fundingRange[1] < 10 ||
    Object.values(riskLevels).some(Boolean);

  const handleClearAll = () => {
    setSelectedIndustries([]);
    setSelectedStages([]);
    setSelectedStatuses([]);
    setAiScoreRange([0, 100]);
    setFundingRange([0, 10]);
    setRiskLevels({ low: false, medium: false, high: false });
  };

  const handleRiskLevelChange = (level: keyof typeof riskLevels) => {
    setRiskLevels(prev => ({ ...prev, [level]: !prev[level] }));
  };

  return (
    <Box sx={{ mb: 3 }}>
      {/* Filter Toggle Button */}
      <Button
        variant="outlined"
        startIcon={<FilterList />}
        endIcon={open ? <ExpandLess /> : <ExpandMore />}
        onClick={onToggle}
        sx={{ mb: 2 }}
      >
        Filters {hasActiveFilters && `(${
          selectedIndustries.length + 
          selectedStages.length + 
          selectedStatuses.length +
          Object.values(riskLevels).filter(Boolean).length
        })`}
      </Button>

      {/* Active Filters Chips */}
      {hasActiveFilters && (
        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 2 }}>
          {selectedIndustries.map(industry => (
            <Chip
              key={industry}
              label={industryOptions.find(opt => opt.value === industry)?.label}
              onDelete={() => setSelectedIndustries(prev => prev.filter(i => i !== industry))}
              size="small"
            />
          ))}
          {selectedStages.map(stage => (
            <Chip
              key={stage}
              label={stageOptions.find(opt => opt.value === stage)?.label}
              onDelete={() => setSelectedStages(prev => prev.filter(s => s !== stage))}
              size="small"
            />
          ))}
          {selectedStatuses.map(status => (
            <Chip
              key={status}
              label={statusOptions.find(opt => opt.value === status)?.label}
              onDelete={() => setSelectedStatuses(prev => prev.filter(s => s !== status))}
              size="small"
            />
          ))}
          {Object.entries(riskLevels).filter(([_, value]) => value).map(([level]) => (
            <Chip
              key={level}
              label={`${level.charAt(0).toUpperCase() + level.slice(1)} Risk`}
              onDelete={() => handleRiskLevelChange(level as keyof typeof riskLevels)}
              size="small"
            />
          ))}
          <Button
            size="small"
            startIcon={<Clear />}
            onClick={handleClearAll}
          >
            Clear All
          </Button>
        </Box>
      )}

      {/* Filter Panel */}
      <Collapse in={open}>
        <Card>
          <CardContent>
            <Box
              sx={{
                display: 'grid',
                gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)', lg: 'repeat(3, 1fr)' },
                gap: 3,
              }}
            >
              {/* Industry Filter */}
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Industry
                </Typography>
                <MultiSelectFilter
                  label="Select Industries"
                  options={industryOptions}
                  selected={selectedIndustries}
                  onChange={setSelectedIndustries}
                />
              </Box>

              {/* Stage Filter */}
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Funding Stage
                </Typography>
                <MultiSelectFilter
                  label="Select Stages"
                  options={stageOptions}
                  selected={selectedStages}
                  onChange={setSelectedStages}
                />
              </Box>

              {/* Status Filter */}
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Status
                </Typography>
                <MultiSelectFilter
                  label="Select Status"
                  options={statusOptions}
                  selected={selectedStatuses}
                  onChange={setSelectedStatuses}
                />
              </Box>

              {/* AI Score Range */}
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  AI Score Range
                </Typography>
                <Box sx={{ px: 2 }}>
                  <Slider
                    value={aiScoreRange}
                    onChange={(_, newValue) => setAiScoreRange(newValue as number[])}
                    valueLabelDisplay="auto"
                    min={0}
                    max={100}
                    marks={[
                      { value: 0, label: '0' },
                      { value: 50, label: '50' },
                      { value: 100, label: '100' },
                    ]}
                  />
                </Box>
              </Box>

              {/* Funding Range */}
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Funding Range (M$)
                </Typography>
                <Box sx={{ px: 2 }}>
                  <Slider
                    value={fundingRange}
                    onChange={(_, newValue) => setFundingRange(newValue as number[])}
                    valueLabelDisplay="auto"
                    min={0}
                    max={10}
                    step={0.5}
                    marks={[
                      { value: 0, label: '$0M' },
                      { value: 5, label: '$5M' },
                      { value: 10, label: '$10M+' },
                    ]}
                  />
                </Box>
              </Box>

              {/* Risk Level */}
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Risk Level
                </Typography>
                <FormGroup>
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={riskLevels.low}
                        onChange={() => handleRiskLevelChange('low')}
                        color="success"
                      />
                    }
                    label="Low Risk"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={riskLevels.medium}
                        onChange={() => handleRiskLevelChange('medium')}
                        color="warning"
                      />
                    }
                    label="Medium Risk"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={riskLevels.high}
                        onChange={() => handleRiskLevelChange('high')}
                        color="error"
                      />
                    }
                    label="High Risk"
                  />
                </FormGroup>
              </Box>
            </Box>

            {/* Filter Actions */}
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 2, mt: 3 }}>
              <Button variant="outlined" onClick={handleClearAll}>
                Clear All
              </Button>
              <Button variant="contained" onClick={onToggle}>
                Apply Filters
              </Button>
            </Box>
          </CardContent>
        </Card>
      </Collapse>
    </Box>
  );
};
