import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { UploadFile } from '@/types';

interface UploadState {
  files: UploadFile[];
  progress: UploadProgress;
  queue: UploadQueue;
  history: UploadHistory[];
  loading: boolean;
  error: string | null;
}

interface UploadProgress {
  overall: number;
  current: UploadFile | null;
  completed: number;
  failed: number;
  total: number;
}

interface UploadQueue {
  pending: UploadFile[];
  processing: UploadFile[];
  maxConcurrent: number;
}

interface UploadHistory {
  id: string;
  fileName: string;
  uploadedAt: Date;
  status: 'success' | 'failed';
  error?: string;
}

const initialState: UploadState = {
  files: [],
  progress: {
    overall: 0,
    current: null,
    completed: 0,
    failed: 0,
    total: 0,
  },
  queue: {
    pending: [],
    processing: [],
    maxConcurrent: 3,
  },
  history: [],
  loading: false,
  error: null,
};

const uploadSlice = createSlice({
  name: 'upload',
  initialState,
  reducers: {
    addFiles: (state, action: PayloadAction<UploadFile[]>) => {
      state.files.push(...action.payload);
      state.queue.pending.push(...action.payload);
      state.progress.total += action.payload.length;
    },
    updateFile: (state, action: PayloadAction<UploadFile>) => {
      const index = state.files.findIndex(f => f.id === action.payload.id);
      if (index !== -1) {
        state.files[index] = action.payload;
      }
    },
    removeFile: (state, action: PayloadAction<string>) => {
      state.files = state.files.filter(f => f.id !== action.payload);
      state.queue.pending = state.queue.pending.filter(f => f.id !== action.payload);
      state.queue.processing = state.queue.processing.filter(f => f.id !== action.payload);
    },
    moveToProcessing: (state, action: PayloadAction<string>) => {
      const fileIndex = state.queue.pending.findIndex(f => f.id === action.payload);
      if (fileIndex !== -1) {
        const file = state.queue.pending.splice(fileIndex, 1)[0];
        state.queue.processing.push(file);
        state.progress.current = file;
      }
    },
    completeFile: (state, action: PayloadAction<{ id: string; success: boolean; error?: string }>) => {
      const { id, success, error } = action.payload;
      
      // Remove from processing
      state.queue.processing = state.queue.processing.filter(f => f.id !== id);
      
      // Update progress
      if (success) {
        state.progress.completed++;
      } else {
        state.progress.failed++;
      }
      
      // Add to history
      const file = state.files.find(f => f.id === id);
      if (file) {
        state.history.unshift({
          id,
          fileName: file.name,
          uploadedAt: new Date(),
          status: success ? 'success' : 'failed',
          error,
        });
      }
      
      // Update overall progress
      const totalProcessed = state.progress.completed + state.progress.failed;
      state.progress.overall = state.progress.total > 0 ? (totalProcessed / state.progress.total) * 100 : 0;
      
      // Clear current if this was the current file
      if (state.progress.current?.id === id) {
        state.progress.current = null;
      }
    },
    updateProgress: (state, action: PayloadAction<{ id: string; progress: number }>) => {
      const file = state.files.find(f => f.id === action.payload.id);
      if (file) {
        file.progress = action.payload.progress;
      }
    },
    clearCompleted: (state) => {
      state.files = state.files.filter(f => f.status === 'pending' || f.status === 'uploading' || f.status === 'processing');
    },
    clearAll: (state) => {
      state.files = [];
      state.queue.pending = [];
      state.queue.processing = [];
      state.progress = {
        overall: 0,
        current: null,
        completed: 0,
        failed: 0,
        total: 0,
      };
    },
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setError: (state, action: PayloadAction<string | null>) => {
      state.error = action.payload;
      state.loading = false;
    },
  },
});

export const {
  addFiles,
  updateFile,
  removeFile,
  moveToProcessing,
  completeFile,
  updateProgress,
  clearCompleted,
  clearAll,
  setLoading,
  setError,
} = uploadSlice.actions;

export default uploadSlice.reducer;
