import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Startup, FilterState } from '@/types';

interface StartupsState {
  list: Startup[];
  selected: Startup | null;
  filters: FilterState;
  loading: boolean;
  error: string | null;
  pagination: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
  };
}

const initialFilters: FilterState = {
  search: '',
  recommendation: [],
  riskLevel: [],
  status: [],
  sector: [],
  stage: [],
  scoreRange: [0, 100],
  dateRange: [null, null],
};

const initialState: StartupsState = {
  list: [],
  selected: null,
  filters: initialFilters,
  loading: false,
  error: null,
  pagination: {
    page: 1,
    limit: 12,
    total: 0,
    totalPages: 0,
  },
};

const startupsSlice = createSlice({
  name: 'startups',
  initialState,
  reducers: {
    setStartups: (state, action: PayloadAction<Startup[]>) => {
      state.list = action.payload;
      state.loading = false;
      state.error = null;
    },
    setSelectedStartup: (state, action: PayloadAction<Startup | null>) => {
      state.selected = action.payload;
    },
    updateStartup: (state, action: PayloadAction<Startup>) => {
      const index = state.list.findIndex(s => s.id === action.payload.id);
      if (index !== -1) {
        state.list[index] = action.payload;
      }
      if (state.selected?.id === action.payload.id) {
        state.selected = action.payload;
      }
    },
    removeStartup: (state, action: PayloadAction<string>) => {
      state.list = state.list.filter(s => s.id !== action.payload);
      if (state.selected?.id === action.payload) {
        state.selected = null;
      }
    },
    setFilters: (state, action: PayloadAction<Partial<FilterState>>) => {
      state.filters = { ...state.filters, ...action.payload };
      state.pagination.page = 1; // Reset to first page when filters change
    },
    clearFilters: (state) => {
      state.filters = initialFilters;
      state.pagination.page = 1;
    },
    setPagination: (state, action: PayloadAction<Partial<StartupsState['pagination']>>) => {
      state.pagination = { ...state.pagination, ...action.payload };
    },
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setError: (state, action: PayloadAction<string | null>) => {
      state.error = action.payload;
      state.loading = false;
    },
  },
});

export const {
  setStartups,
  setSelectedStartup,
  updateStartup,
  removeStartup,
  setFilters,
  clearFilters,
  setPagination,
  setLoading,
  setError,
} = startupsSlice.actions;

export default startupsSlice.reducer;
