import { useState, useCallback } from 'react';
import {
  Box,
  Card,
  Typography,
  Button,
  Chip,
  Alert,
} from '@mui/material';
import { CloudUpload, Description, Error } from '@mui/icons-material';

interface UploadDropzoneProps {
  onFilesSelected: (files: File[]) => void;
}

const ACCEPTED_TYPES = [
  'application/pdf',
  'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
  'application/msword',
  'application/vnd.ms-excel',
  'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  'text/csv',
  'application/vnd.ms-powerpoint',
  'application/vnd.openxmlformats-officedocument.presentationml.presentation',
];

const MAX_FILE_SIZE = 10 * 1024 * 1024; // 10MB
const MAX_FILES = 10;

export const UploadDropzone: React.FC<UploadDropzoneProps> = ({ onFilesSelected }) => {
  const [isDragOver, setIsDragOver] = useState(false);
  const [error, setError] = useState<string>('');

  const validateFiles = (files: FileList): File[] => {
    const validFiles: File[] = [];
    const errors: string[] = [];

    if (files.length > MAX_FILES) {
      errors.push(`Maximum ${MAX_FILES} files allowed`);
      return [];
    }

    Array.from(files).forEach((file) => {
      if (!ACCEPTED_TYPES.includes(file.type)) {
        errors.push(`${file.name}: Unsupported file type`);
        return;
      }

      if (file.size > MAX_FILE_SIZE) {
        errors.push(`${file.name}: File too large (max 10MB)`);
        return;
      }

      validFiles.push(file);
    });

    if (errors.length > 0) {
      setError(errors.join(', '));
      return [];
    }

    setError('');
    return validFiles;
  };

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);

    const files = validateFiles(e.dataTransfer.files);
    if (files.length > 0) {
      onFilesSelected(files);
    }
  }, [onFilesSelected]);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  }, []);

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = validateFiles(e.target.files);
      if (files.length > 0) {
        onFilesSelected(files);
      }
    }
  };

  return (
    <Box>
      <Card
        sx={{
          border: '2px dashed',
          borderColor: isDragOver ? 'primary.main' : 'grey.300',
          bgcolor: isDragOver ? 'primary.50' : 'background.paper',
          transition: 'all 0.3s ease',
          cursor: 'pointer',
          '&:hover': {
            borderColor: 'primary.main',
            bgcolor: 'primary.50',
          },
        }}
        onDrop={handleDrop}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
      >
        <Box sx={{ p: 6, textAlign: 'center' }}>
          <CloudUpload
            sx={{
              fontSize: 80,
              color: isDragOver ? 'primary.main' : 'grey.400',
              mb: 2,
            }}
          />
          
          <Typography variant="h5" gutterBottom>
            Drop files here or click to browse
          </Typography>
          
          <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
            Upload startup documents for AI analysis
          </Typography>

          <Button
            variant="contained"
            size="large"
            component="label"
            startIcon={<Description />}
            sx={{ mb: 3 }}
          >
            Select Files
            <input
              type="file"
              hidden
              multiple
              accept={ACCEPTED_TYPES.join(',')}
              onChange={handleFileSelect}
            />
          </Button>

          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, justifyContent: 'center' }}>
            <Chip label="PDF" size="small" />
            <Chip label="Word" size="small" />
            <Chip label="Excel" size="small" />
            <Chip label="PowerPoint" size="small" />
            <Chip label="CSV" size="small" />
          </Box>

          <Typography variant="caption" color="text.secondary" sx={{ mt: 2, display: 'block' }}>
            Maximum file size: 10MB  Maximum files: {MAX_FILES}
          </Typography>
        </Box>
      </Card>

      {error && (
        <Alert 
          severity="error" 
          icon={<Error />}
          sx={{ mt: 2 }}
          onClose={() => setError('')}
        >
          {error}
        </Alert>
      )}

      {/* File Type Guide */}
      <Card sx={{ mt: 3 }}>
        <Box sx={{ p: 3 }}>
          <Typography variant="h6" gutterBottom>
            Supported Document Types
          </Typography>
          <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: 2 }}>
            <Box>
              <Typography variant="subtitle2" color="primary">Financial Documents</Typography>
              <Typography variant="body2" color="text.secondary">
                 Financial statements<br/>
                 Revenue reports<br/>
                 Cash flow statements
              </Typography>
            </Box>
            <Box>
              <Typography variant="subtitle2" color="primary">Business Plans</Typography>
              <Typography variant="body2" color="text.secondary">
                 Executive summaries<br/>
                 Market analysis<br/>
                 Growth strategies
              </Typography>
            </Box>
            <Box>
              <Typography variant="subtitle2" color="primary">Legal Documents</Typography>
              <Typography variant="body2" color="text.secondary">
                 Incorporation papers<br/>
                 Contracts<br/>
                 IP documentation
              </Typography>
            </Box>
            <Box>
              <Typography variant="subtitle2" color="primary">Pitch Materials</Typography>
              <Typography variant="body2" color="text.secondary">
                 Pitch decks<br/>
                 Investor presentations<br/>
                 Demo materials
              </Typography>
            </Box>
          </Box>
        </Box>
      </Card>
    </Box>
  );
};
