import { useSelector, useDispatch } from 'react-redux';
import { useCallback } from 'react';
import { RootState } from '@/store';
import { loginStart, loginSuccess, loginFailure, logout, updateUser } from '@/store/slices/authSlice';
import { mockApi } from '@/services/mock/mockApi';
import { User } from '@/types';

export const useAuth = () => {
  const dispatch = useDispatch();
  const { user, token, isAuthenticated, loading, error } = useSelector(
    (state: RootState) => state.auth
  );

  const login = useCallback(async (email: string, password: string) => {
    try {
      dispatch(loginStart());
      
      // Use mock API for development
      const response = await mockApi.login(email, password);
      
      dispatch(loginSuccess({
        user: response.data.user,
        token: response.data.token,
      }));
      
      return { success: true };
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Login failed';
      dispatch(loginFailure(errorMessage));
      return { success: false, error: errorMessage };
    }
  }, [dispatch]);

  const logoutUser = useCallback(() => {
    dispatch(logout());
  }, [dispatch]);

  const updateUserProfile = useCallback(async (updates: Partial<User>) => {
    try {
      const response = await mockApi.updateProfile(updates);
      dispatch(updateUser(response.data));
      return { success: true };
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Update failed';
      return { success: false, error: errorMessage };
    }
  }, [dispatch]);

  const checkAuthStatus = useCallback(() => {
    const storedToken = localStorage.getItem('token');
    if (storedToken && !isAuthenticated) {
      // In a real app, you would validate the token with the server
      // For now, we'll just assume it's valid if it exists
      // This is where you'd typically make an API call to validate the token
    }
  }, [isAuthenticated]);

  return {
    user,
    token,
    isAuthenticated,
    loading,
    error,
    login,
    logout: logoutUser,
    updateProfile: updateUserProfile,
    checkAuthStatus,
  };
};
