import {
  Box,
  Typography,
  Card,
  CardContent,
  Chip,
  Avatar,
  IconButton,
  Menu,
  MenuItem,
  LinearProgress,
} from '@mui/material';
import { useState } from 'react';
import {
  PictureAsPdf,
  MoreVert,
  Download,
  Share,
  Delete,
  Visibility,
} from '@mui/icons-material';

const reports = [
  {
    id: '1',
    title: 'Q3 2024 Portfolio Analysis',
    type: 'Portfolio Report',
    createdAt: new Date('2024-01-15'),
    status: 'completed',
    pages: 24,
    size: '2.4 MB',
    sharedWith: ['john@company.com', 'sarah@company.com'],
  },
  {
    id: '2',
    title: 'TechFlow Solutions Deep Dive',
    type: 'Startup Analysis',
    createdAt: new Date('2024-01-12'),
    status: 'completed',
    pages: 18,
    size: '1.8 MB',
    sharedWith: ['investor@fund.com'],
  },
  {
    id: '3',
    title: 'Risk Assessment Report',
    type: 'Risk Analysis',
    createdAt: new Date('2024-01-10'),
    status: 'generating',
    pages: 0,
    size: '0 MB',
    progress: 65,
    sharedWith: [],
  },
  {
    id: '4',
    title: 'Market Comparison Study',
    type: 'Comparative Analysis',
    createdAt: new Date('2024-01-08'),
    status: 'failed',
    pages: 0,
    size: '0 MB',
    error: 'Data processing error',
    sharedWith: [],
  },
];

export const ReportHistory: React.FC = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'success';
      case 'generating': return 'warning';
      case 'failed': return 'error';
      default: return 'default';
    }
  };

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
        Report History
      </Typography>

      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        {reports.map((report) => (
          <Card key={report.id}>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Avatar sx={{ bgcolor: 'info.main', mr: 2 }}>
                  <PictureAsPdf />
                </Avatar>
                
                <Box sx={{ flex: 1 }}>
                  <Typography variant="subtitle1" fontWeight="500">
                    {report.title}
                  </Typography>
                  
                  <Box sx={{ display: 'flex', gap: 1, mt: 0.5, mb: 1 }}>
                    <Chip size="small" label={report.type} />
                    <Chip
                      size="small"
                      label={report.status}
                      color={getStatusColor(report.status) as any}
                    />
                    {report.pages > 0 && (
                      <Chip size="small" label={`${report.pages} pages`} variant="outlined" />
                    )}
                  </Box>

                  {report.status === 'generating' && report.progress && (
                    <Box sx={{ mt: 1 }}>
                      <LinearProgress variant="determinate" value={report.progress} />
                      <Typography variant="caption" color="text.secondary">
                        {report.progress}% complete
                      </Typography>
                    </Box>
                  )}

                  {report.status === 'failed' && report.error && (
                    <Typography variant="caption" color="error">
                      Error: {report.error}
                    </Typography>
                  )}

                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 1 }}>
                    <Typography variant="caption" color="text.secondary">
                      Created: {report.createdAt.toLocaleDateString()}  Size: {report.size}
                    </Typography>
                    {report.sharedWith.length > 0 && (
                      <Typography variant="caption" color="text.secondary">
                        Shared with {report.sharedWith.length} people
                      </Typography>
                    )}
                  </Box>
                </Box>

                <IconButton
                  onClick={handleMenuOpen}
                  disabled={report.status === 'generating'}
                >
                  <MoreVert />
                </IconButton>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={handleMenuClose}>
          <Visibility sx={{ mr: 1 }} />
          View Report
        </MenuItem>
        <MenuItem onClick={handleMenuClose}>
          <Download sx={{ mr: 1 }} />
          Download
        </MenuItem>
        <MenuItem onClick={handleMenuClose}>
          <Share sx={{ mr: 1 }} />
          Share
        </MenuItem>
        <MenuItem onClick={handleMenuClose} sx={{ color: 'error.main' }}>
          <Delete sx={{ mr: 1 }} />
          Delete
        </MenuItem>
      </Menu>
    </Box>
  );
};
