import { useState } from 'react';
import { useParams } from 'react-router-dom';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Tabs,
  Tab,
  Chip,
  Avatar,
  Button,
} from '@mui/material';
import {
  TrendingUp,
  Assessment,
  CompareArrows,
  Insights,
  Download,
  Share,
} from '@mui/icons-material';
import { InsightsPanel } from './components/InsightsPanel';
import { FinancialCharts } from './components/FinancialCharts';
import { TractionCharts } from './components/TractionCharts';
import { ComparisonCharts } from './components/ComparisonCharts';
import { RiskVisualization } from './components/RiskVisualization';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

const TabPanel: React.FC<TabPanelProps> = ({ children, value, index }) => (
  <div hidden={value !== index}>
    {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
  </div>
);

export const AnalysisPage: React.FC = () => {
  const { startupId } = useParams();
  const [activeTab, setActiveTab] = useState(0);

  // Mock startup data
  const startup = {
    id: startupId || '1',
    name: 'TechFlow Solutions',
    industry: 'SaaS',
    aiScore: 85,
    riskLevel: 'Low',
    lastAnalyzed: new Date(),
  };

  const tabs = [
    { label: 'Financial', icon: <TrendingUp />, component: <FinancialCharts /> },
    { label: 'Traction', icon: <Assessment />, component: <TractionCharts /> },
    { label: 'Risk Analysis', icon: <Insights />, component: <RiskVisualization /> },
    { label: 'Benchmarks', icon: <CompareArrows />, component: <ComparisonCharts /> },
  ];

  return (
    <Box sx={{ width: '100%', minHeight: '100%' }}>
      {/* Header */}
      <Box sx={{ mb: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 3 }}>
          <Box>
            <Typography variant="h4" fontWeight="bold" gutterBottom>
              Analysis Dashboard
            </Typography>
            <Typography variant="body1" color="text.secondary">
              Comprehensive AI-powered analysis and insights for {startup.name}
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', gap: 1 }}>
            <Button variant="outlined" startIcon={<Share />}>
              Share
            </Button>
            <Button variant="contained" startIcon={<Download />}>
              Export Report
            </Button>
          </Box>
        </Box>

        {/* Startup Info Card */}
        <Card sx={{ 
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          color: 'white',
        }}>
          <CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
              <Avatar sx={{ width: 64, height: 64, bgcolor: 'rgba(255,255,255,0.2)' }}>
                {startup.name.charAt(0)}
              </Avatar>
              <Box sx={{ flex: 1 }}>
                <Typography variant="h5" fontWeight="bold" gutterBottom>
                  {startup.name}
                </Typography>
                <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
                  <Chip 
                    label={startup.industry} 
                    sx={{ bgcolor: 'rgba(255,255,255,0.2)', color: 'white' }}
                  />
                  <Chip 
                    label={`Risk: ${startup.riskLevel}`}
                    sx={{ bgcolor: 'rgba(255,255,255,0.2)', color: 'white' }}
                  />
                </Box>
              </Box>
              <Box sx={{ textAlign: 'right' }}>
                <Typography variant="h3" fontWeight="bold">
                  {startup.aiScore}
                </Typography>
                <Typography variant="body2" sx={{ opacity: 0.9 }}>
                  AI Score
                </Typography>
              </Box>
            </Box>
          </CardContent>
        </Card>
      </Box>

      <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', lg: '2fr 1fr' }, gap: 3 }}>
        {/* Main Analysis Area */}
        <Box>
          <Card>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
              <Tabs 
                value={activeTab} 
                onChange={(_, newValue) => setActiveTab(newValue)}
                variant="scrollable"
                scrollButtons="auto"
              >
                {tabs.map((tab, index) => (
                  <Tab
                    key={index}
                    icon={tab.icon}
                    label={tab.label}
                    iconPosition="start"
                    sx={{ minHeight: 64 }}
                  />
                ))}
              </Tabs>
            </Box>

            {tabs.map((tab, index) => (
              <TabPanel key={index} value={activeTab} index={index}>
                {tab.component}
              </TabPanel>
            ))}
          </Card>
        </Box>

        {/* Insights Sidebar */}
        <Box>
          <InsightsPanel startupId={startup.id} />
        </Box>
      </Box>
    </Box>
  );
};
