import { useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Checkbox,
  FormControlLabel,
  Button,
  Stepper,
  Step,
  StepLabel,
  Chip,
} from '@mui/material';
import { Assessment, PictureAsPdf, Description } from '@mui/icons-material';

const steps = ['Select Template', 'Choose Data', 'Configure Report'];

const templates = [
  {
    id: 'portfolio',
    name: 'Portfolio Overview',
    description: 'Comprehensive analysis of entire portfolio performance',
    icon: <Assessment />,
    sections: ['Executive Summary', 'Portfolio Metrics', 'Risk Analysis', 'Recommendations'],
  },
  {
    id: 'startup',
    name: 'Startup Deep Dive',
    description: 'Detailed analysis of individual startup performance',
    icon: <Description />,
    sections: ['Company Overview', 'Financial Analysis', 'Market Position', 'Growth Projections'],
  },
  {
    id: 'comparison',
    name: 'Comparative Analysis',
    description: 'Side-by-side comparison of multiple startups',
    icon: <PictureAsPdf />,
    sections: ['Performance Comparison', 'Risk Assessment', 'Investment Recommendations'],
  },
];

const mockStartups = [
  { id: '1', name: 'TechFlow Solutions', industry: 'SaaS' },
  { id: '2', name: 'HealthBridge', industry: 'HealthTech' },
  { id: '3', name: 'EduNext', industry: 'EdTech' },
  { id: '4', name: 'GreenEnergy Co', industry: 'CleanTech' },
];

export const ReportGenerator: React.FC = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [selectedTemplate, setSelectedTemplate] = useState('');
  const [selectedStartups, setSelectedStartups] = useState<string[]>([]);
  const [reportTitle, setReportTitle] = useState('');
  const [reportFormat, setReportFormat] = useState('pdf');
  const [includeSections, setIncludeSections] = useState<string[]>([]);

  const handleNext = () => {
    setActiveStep((prev) => prev + 1);
  };

  const handleBack = () => {
    setActiveStep((prev) => prev - 1);
  };

  const handleTemplateSelect = (templateId: string) => {
    setSelectedTemplate(templateId);
    const template = templates.find(t => t.id === templateId);
    if (template) {
      setIncludeSections(template.sections);
    }
  };

  const handleStartupToggle = (startupId: string) => {
    setSelectedStartups(prev =>
      prev.includes(startupId)
        ? prev.filter(id => id !== startupId)
        : [...prev, startupId]
    );
  };

  const handleSectionToggle = (section: string) => {
    setIncludeSections(prev =>
      prev.includes(section)
        ? prev.filter(s => s !== section)
        : [...prev, section]
    );
  };

  const handleGenerate = () => {
    console.log('Generating report:', {
      template: selectedTemplate,
      startups: selectedStartups,
      title: reportTitle,
      format: reportFormat,
      sections: includeSections,
    });
  };

  return (
    <Box>
      <Stepper activeStep={activeStep} sx={{ mb: 4 }}>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>

      {/* Step 1: Select Template */}
      {activeStep === 0 && (
        <Box>
          <Typography variant="h6" gutterBottom>
            Choose Report Template
          </Typography>
          <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))', gap: 2 }}>
            {templates.map((template) => (
              <Card
                key={template.id}
                sx={{
                  cursor: 'pointer',
                  border: selectedTemplate === template.id ? '2px solid' : '1px solid',
                  borderColor: selectedTemplate === template.id ? 'primary.main' : 'divider',
                }}
                onClick={() => handleTemplateSelect(template.id)}
              >
                <CardContent>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    {template.icon}
                    <Typography variant="h6" sx={{ ml: 1 }}>
                      {template.name}
                    </Typography>
                  </Box>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                    {template.description}
                  </Typography>
                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                    {template.sections.map((section) => (
                      <Chip key={section} label={section} size="small" />
                    ))}
                  </Box>
                </CardContent>
              </Card>
            ))}
          </Box>
        </Box>
      )}

      {/* Step 2: Choose Data */}
      {activeStep === 1 && (
        <Box>
          <Typography variant="h6" gutterBottom>
            Select Startups to Include
          </Typography>
          <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: 2 }}>
            {mockStartups.map((startup) => (
              <Card key={startup.id}>
                <CardContent>
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={selectedStartups.includes(startup.id)}
                        onChange={() => handleStartupToggle(startup.id)}
                      />
                    }
                    label={
                      <Box>
                        <Typography variant="subtitle1">{startup.name}</Typography>
                        <Typography variant="body2" color="text.secondary">
                          {startup.industry}
                        </Typography>
                      </Box>
                    }
                  />
                </CardContent>
              </Card>
            ))}
          </Box>
        </Box>
      )}

      {/* Step 3: Configure Report */}
      {activeStep === 2 && (
        <Box>
          <Typography variant="h6" gutterBottom>
            Configure Report Settings
          </Typography>
          <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 3 }}>
            <Box>
              <TextField
                fullWidth
                label="Report Title"
                value={reportTitle}
                onChange={(e) => setReportTitle(e.target.value)}
                sx={{ mb: 3 }}
              />
              <FormControl fullWidth sx={{ mb: 3 }}>
                <InputLabel>Format</InputLabel>
                <Select
                  value={reportFormat}
                  onChange={(e) => setReportFormat(e.target.value)}
                  label="Format"
                >
                  <MenuItem value="pdf">PDF</MenuItem>
                  <MenuItem value="docx">Word Document</MenuItem>
                  <MenuItem value="pptx">PowerPoint</MenuItem>
                </Select>
              </FormControl>
            </Box>
            <Box>
              <Typography variant="subtitle1" gutterBottom>
                Include Sections
              </Typography>
              {templates
                .find(t => t.id === selectedTemplate)
                ?.sections.map((section) => (
                  <FormControlLabel
                    key={section}
                    control={
                      <Checkbox
                        checked={includeSections.includes(section)}
                        onChange={() => handleSectionToggle(section)}
                      />
                    }
                    label={section}
                    sx={{ display: 'block' }}
                  />
                ))}
            </Box>
          </Box>
        </Box>
      )}

      {/* Navigation */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 4 }}>
        <Button
          disabled={activeStep === 0}
          onClick={handleBack}
        >
          Back
        </Button>
        {activeStep === steps.length - 1 ? (
          <Button
            variant="contained"
            onClick={handleGenerate}
            disabled={!selectedTemplate || selectedStartups.length === 0 || !reportTitle}
          >
            Generate Report
          </Button>
        ) : (
          <Button
            variant="contained"
            onClick={handleNext}
            disabled={
              (activeStep === 0 && !selectedTemplate) ||
              (activeStep === 1 && selectedStartups.length === 0)
            }
          >
            Next
          </Button>
        )}
      </Box>
    </Box>
  );
};
