import { useState } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  LinearProgress,
  Chip,
  Avatar,
} from '@mui/material';
import {
  Description,
  PictureAsPdf,
  TableChart,
  Slideshow,
  Delete,
  CheckCircle,
  Error,
  Schedule,
} from '@mui/icons-material';

interface FileQueueProps {
  files: File[];
}

interface FileStatus {
  id: string;
  status: 'pending' | 'uploading' | 'processing' | 'completed' | 'error';
  progress: number;
  error?: string;
}

export const FileQueue: React.FC<FileQueueProps> = ({ files }) => {
  const [fileStatuses] = useState<FileStatus[]>(
    files.map((file, index) => ({
      id: `${file.name}-${index}`,
      status: Math.random() > 0.8 ? 'error' : Math.random() > 0.6 ? 'completed' : Math.random() > 0.3 ? 'processing' : 'uploading',
      progress: Math.random() * 100,
      error: Math.random() > 0.8 ? 'Failed to process document' : undefined,
    }))
  );

  const getFileIcon = (file: File) => {
    const type = file.type;
    if (type.includes('pdf')) return <PictureAsPdf color="error" />;
    if (type.includes('sheet') || type.includes('excel')) return <TableChart color="success" />;
    if (type.includes('presentation') || type.includes('powerpoint')) return <Slideshow color="warning" />;
    return <Description color="primary" />;
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'completed': return <CheckCircle color="success" />;
      case 'error': return <Error color="error" />;
      case 'processing': return <Schedule color="warning" />;
      default: return undefined;
    }
  };

  const getStatusColor = (status: string): 'success' | 'error' | 'warning' | 'info' | 'default' => {
    switch (status) {
      case 'completed': return 'success';
      case 'error': return 'error';
      case 'processing': return 'warning';
      case 'uploading': return 'info';
      default: return 'default';
    }
  };

  const formatFileSize = (bytes: number) => {
    const mb = bytes / (1024 * 1024);
    return mb < 1 ? `${(bytes / 1024).toFixed(1)} KB` : `${mb.toFixed(1)} MB`;
  };

  const handleRemoveFile = (index: number) => {
    console.log('Remove file:', files[index].name);
  };

  return (
    <Card>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6">
            File Queue ({files.length})
          </Typography>
          <Box sx={{ display: 'flex', gap: 1 }}>
            <Chip 
              label={`${fileStatuses.filter(f => f.status === 'completed').length} completed`}
              size="small" 
              color="success" 
            />
            <Chip 
              label={`${fileStatuses.filter(f => f.status === 'error').length} errors`}
              size="small" 
              color="error" 
            />
          </Box>
        </Box>

        <List>
          {files.map((file, index) => {
            const status = fileStatuses[index];
            
            return (
              <ListItem
                key={`${file.name}-${index}`}
                sx={{
                  border: '1px solid',
                  borderColor: 'divider',
                  borderRadius: 2,
                  mb: 1,
                  bgcolor: status?.status === 'error' ? 'error.50' : 'background.paper',
                }}
              >
                <ListItemIcon>
                  <Avatar sx={{ bgcolor: 'transparent' }}>
                    {getFileIcon(file)}
                  </Avatar>
                </ListItemIcon>

                <ListItemText
                  primary={
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Typography variant="body1" noWrap>
                        {file.name}
                      </Typography>
                      <Chip
                        size="small"
                        label={status?.status || 'pending'}
                        color={getStatusColor(status?.status || 'default')}
                        icon={getStatusIcon(status?.status || 'pending')}
                      />
                    </Box>
                  }
                  secondary={
                    <Box>
                      <Typography variant="caption" color="text.secondary">
                        {formatFileSize(file.size)}  {file.type.split('/')[1]?.toUpperCase()}
                      </Typography>
                      
                      {status?.status === 'error' && status.error && (
                        <Typography variant="caption" color="error" sx={{ display: 'block' }}>
                          {status.error}
                        </Typography>
                      )}

                      {(status?.status === 'uploading' || status?.status === 'processing') && (
                        <Box sx={{ mt: 1 }}>
                          <LinearProgress
                            variant="determinate"
                            value={status.progress}
                            sx={{ height: 4, borderRadius: 2 }}
                          />
                          <Typography variant="caption" color="text.secondary">
                            {Math.round(status.progress)}% {status.status}
                          </Typography>
                        </Box>
                      )}
                    </Box>
                  }
                />

                <ListItemSecondaryAction>
                  <IconButton
                    edge="end"
                    onClick={() => handleRemoveFile(index)}
                    size="small"
                  >
                    <Delete />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            );
          })}
        </List>

        {files.length === 0 && (
          <Box sx={{ textAlign: 'center', py: 4 }}>
            <Typography variant="body2" color="text.secondary">
              No files in queue
            </Typography>
          </Box>
        )}
      </CardContent>
    </Card>
  );
};
