import { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  LinearProgress,
  Chip,
  Avatar,
} from '@mui/material';
import {
  CloudUpload,
  Assessment,
  CheckCircle,
  Speed,
} from '@mui/icons-material';

interface ProgressTrackerProps {
  files: File[];
  onComplete: () => void;
}

interface UploadProgress {
  upload: number;
  processing: number;
  analysis: number;
}

export const ProgressTracker: React.FC<ProgressTrackerProps> = ({ files, onComplete }) => {
  const [progress, setProgress] = useState<UploadProgress>({
    upload: 0,
    processing: 0,
    analysis: 0,
  });
  const [currentPhase, setCurrentPhase] = useState<'upload' | 'processing' | 'analysis' | 'complete'>('upload');
  const [uploadSpeed, setUploadSpeed] = useState(0);
  const [timeRemaining, setTimeRemaining] = useState(0);

  useEffect(() => {
    // Simulate upload progress
    const uploadInterval = setInterval(() => {
      setProgress(prev => {
        const newUpload = Math.min(prev.upload + Math.random() * 15, 100);
        
        // Calculate upload speed (MB/s)
        const totalSize = files.reduce((sum, file) => sum + file.size, 0);
        const uploadedSize = (totalSize * newUpload) / 100;
        setUploadSpeed(uploadedSize / (1024 * 1024) / 2); // Simulated speed
        
        // Calculate time remaining
        if (newUpload < 100) {
          const remainingSize = totalSize - uploadedSize;
          setTimeRemaining(remainingSize / (uploadedSize / 2));
        }

        if (newUpload >= 100 && currentPhase === 'upload') {
          setCurrentPhase('processing');
        }

        return { ...prev, upload: newUpload };
      });
    }, 200);

    return () => clearInterval(uploadInterval);
  }, [files, currentPhase]);

  useEffect(() => {
    if (currentPhase === 'processing') {
      const processingInterval = setInterval(() => {
        setProgress(prev => {
          const newProcessing = Math.min(prev.processing + Math.random() * 10, 100);
          
          if (newProcessing >= 100 && currentPhase === 'processing') {
            setCurrentPhase('analysis');
          }

          return { ...prev, processing: newProcessing };
        });
      }, 300);

      return () => clearInterval(processingInterval);
    }
  }, [currentPhase]);

  useEffect(() => {
    if (currentPhase === 'analysis') {
      const analysisInterval = setInterval(() => {
        setProgress(prev => {
          const newAnalysis = Math.min(prev.analysis + Math.random() * 8, 100);
          
          if (newAnalysis >= 100) {
            setCurrentPhase('complete');
            setTimeout(onComplete, 1000);
          }

          return { ...prev, analysis: newAnalysis };
        });
      }, 400);

      return () => clearInterval(analysisInterval);
    }
  }, [currentPhase, onComplete]);

  const getPhaseIcon = (phase: string) => {
    switch (phase) {
      case 'upload': return <CloudUpload />;
      case 'processing': return <Speed />;
      case 'analysis': return <Assessment />;
      case 'complete': return <CheckCircle />;
      default: return <CloudUpload />;
    }
  };

  const getPhaseColor = (phase: string, isActive: boolean) => {
    if (!isActive) return 'grey.300';
    switch (phase) {
      case 'upload': return 'info.main';
      case 'processing': return 'warning.main';
      case 'analysis': return 'primary.main';
      case 'complete': return 'success.main';
      default: return 'grey.300';
    }
  };

  const totalFiles = files.length;
  const totalSize = files.reduce((sum, file) => sum + file.size, 0);
  const formatSize = (bytes: number) => {
    const mb = bytes / (1024 * 1024);
    return mb < 1 ? `${(bytes / 1024).toFixed(1)} KB` : `${mb.toFixed(1)} MB`;
  };

  return (
    <Card>
      <CardContent>
        <Typography variant="h6" gutterBottom>
          Upload Progress
        </Typography>

        {/* Overall Progress */}
        <Box sx={{ mb: 4 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2">
              {totalFiles} files  {formatSize(totalSize)}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {currentPhase === 'upload' && uploadSpeed > 0 && (
                `${uploadSpeed.toFixed(1)} MB/s  ${Math.round(timeRemaining / 1000)}s remaining`
              )}
            </Typography>
          </Box>
          
          <LinearProgress
            variant="determinate"
            value={
              currentPhase === 'complete' ? 100 :
              currentPhase === 'analysis' ? 75 + (progress.analysis * 0.25) :
              currentPhase === 'processing' ? 50 + (progress.processing * 0.25) :
              progress.upload * 0.5
            }
            sx={{ height: 8, borderRadius: 4 }}
          />
        </Box>

        {/* Phase Progress */}
        <Box sx={{ display: 'flex', gap: 3, mb: 3 }}>
          {[
            { key: 'upload', label: 'Upload', progress: progress.upload },
            { key: 'processing', label: 'Processing', progress: progress.processing },
            { key: 'analysis', label: 'AI Analysis', progress: progress.analysis },
          ].map((phase) => {
            const isActive = 
              (currentPhase === 'upload' && phase.key === 'upload') ||
              (currentPhase === 'processing' && ['upload', 'processing'].includes(phase.key)) ||
              (currentPhase === 'analysis' && ['upload', 'processing', 'analysis'].includes(phase.key)) ||
              (currentPhase === 'complete');

            const isCurrentPhase = currentPhase === phase.key;

            return (
              <Box key={phase.key} sx={{ flex: 1 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                  <Avatar
                    sx={{
                      width: 32,
                      height: 32,
                      bgcolor: getPhaseColor(phase.key, isActive),
                      mr: 1,
                    }}
                  >
                    {getPhaseIcon(phase.key)}
                  </Avatar>
                  <Box>
                    <Typography variant="body2" fontWeight={isCurrentPhase ? 'bold' : 'normal'}>
                      {phase.label}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {Math.round(phase.progress)}%
                    </Typography>
                  </Box>
                </Box>
                <LinearProgress
                  variant="determinate"
                  value={phase.progress}
                  sx={{
                    height: 4,
                    borderRadius: 2,
                    bgcolor: 'grey.200',
                    '& .MuiLinearProgress-bar': {
                      bgcolor: getPhaseColor(phase.key, isActive),
                    },
                  }}
                />
              </Box>
            );
          })}
        </Box>

        {/* Status Messages */}
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <Chip
            icon={getPhaseIcon(currentPhase)}
            label={
              currentPhase === 'upload' ? 'Uploading files...' :
              currentPhase === 'processing' ? 'Processing documents...' :
              currentPhase === 'analysis' ? 'Running AI analysis...' :
              'Analysis complete!'
            }
            color={
              currentPhase === 'upload' ? 'info' :
              currentPhase === 'processing' ? 'warning' :
              currentPhase === 'analysis' ? 'primary' :
              'success'
            }
            variant="filled"
          />
        </Box>
      </CardContent>
    </Card>
  );
};
