import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Analysis, ProgressState } from '@/types';

interface AnalysisState {
  results: Analysis[];
  current: Analysis | null;
  progress: ProgressState | null;
  queue: QueueItem[];
  loading: boolean;
  error: string | null;
}

interface QueueItem {
  id: string;
  startupId: string;
  status: 'pending' | 'processing' | 'completed' | 'failed';
  priority: number;
  createdAt: Date;
}

const initialState: AnalysisState = {
  results: [],
  current: null,
  progress: null,
  queue: [],
  loading: false,
  error: null,
};

const analysisSlice = createSlice({
  name: 'analysis',
  initialState,
  reducers: {
    setAnalysisResults: (state, action: PayloadAction<Analysis[]>) => {
      state.results = action.payload;
      state.loading = false;
      state.error = null;
    },
    setCurrentAnalysis: (state, action: PayloadAction<Analysis | null>) => {
      state.current = action.payload;
    },
    addAnalysisResult: (state, action: PayloadAction<Analysis>) => {
      const existingIndex = state.results.findIndex(r => r.startupId === action.payload.startupId);
      if (existingIndex !== -1) {
        state.results[existingIndex] = action.payload;
      } else {
        state.results.push(action.payload);
      }
    },
    updateProgress: (state, action: PayloadAction<ProgressState>) => {
      state.progress = action.payload;
    },
    clearProgress: (state) => {
      state.progress = null;
    },
    addToQueue: (state, action: PayloadAction<Omit<QueueItem, 'createdAt'>>) => {
      const queueItem: QueueItem = {
        ...action.payload,
        createdAt: new Date(),
      };
      state.queue.push(queueItem);
    },
    updateQueueItem: (state, action: PayloadAction<{ id: string; updates: Partial<QueueItem> }>) => {
      const index = state.queue.findIndex(item => item.id === action.payload.id);
      if (index !== -1) {
        state.queue[index] = { ...state.queue[index], ...action.payload.updates };
      }
    },
    removeFromQueue: (state, action: PayloadAction<string>) => {
      state.queue = state.queue.filter(item => item.id !== action.payload);
    },
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setError: (state, action: PayloadAction<string | null>) => {
      state.error = action.payload;
      state.loading = false;
    },
  },
});

export const {
  setAnalysisResults,
  setCurrentAnalysis,
  addAnalysisResult,
  updateProgress,
  clearProgress,
  addToQueue,
  updateQueueItem,
  removeFromQueue,
  setLoading,
  setError,
} = analysisSlice.actions;

export default analysisSlice.reducer;
