import { Button, CircularProgress, Fab, IconButton, ButtonGroup } from '@mui/material';
import { Add, Download, Share, Delete } from '@mui/icons-material';

interface LoadingButtonProps {
  loading?: boolean;
  children: React.ReactNode;
  onClick?: () => void;
  variant?: 'contained' | 'outlined' | 'text';
  color?: 'primary' | 'secondary' | 'error' | 'warning' | 'info' | 'success';
  size?: 'small' | 'medium' | 'large';
  disabled?: boolean;
  startIcon?: React.ReactNode;
  endIcon?: React.ReactNode;
}

export const LoadingButton: React.FC<LoadingButtonProps> = ({
  loading,
  children,
  onClick,
  variant = 'contained',
  color = 'primary',
  size = 'medium',
  disabled,
  startIcon,
  endIcon,
}) => (
  <Button
    variant={variant}
    color={color}
    size={size}
    onClick={onClick}
    disabled={disabled || loading}
    startIcon={loading ? <CircularProgress size={16} /> : startIcon}
    endIcon={!loading ? endIcon : undefined}
  >
    {children}
  </Button>
);

export const ActionButtons: React.FC<{
  onDelete?: () => void;
  onShare?: () => void;
  onDownload?: () => void;
}> = ({ onDelete, onShare, onDownload }) => (
  <ButtonGroup size="small">
    {onDownload && (
      <IconButton onClick={onDownload} title="Download">
        <Download />
      </IconButton>
    )}
    {onShare && (
      <IconButton onClick={onShare} title="Share">
        <Share />
      </IconButton>
    )}
    {onDelete && (
      <IconButton onClick={onDelete} color="error" title="Delete">
        <Delete />
      </IconButton>
    )}
  </ButtonGroup>
);

export const FloatingActionButton: React.FC<{
  onClick: () => void;
  icon?: React.ReactNode;
}> = ({ onClick, icon = <Add /> }) => (
  <Fab
    color="primary"
    onClick={onClick}
    sx={{
      position: 'fixed',
      bottom: 24,
      right: 24,
    }}
  >
    {icon}
  </Fab>
);
