import { mockApiResponses, generateMockAnalysis } from './mockData';
import { Startup, Analysis, User, Report, UploadFile, APIResponse, PaginatedResponse } from '@/types';

// Simulate network delay
const delay = (ms: number = 500) => new Promise(resolve => setTimeout(resolve, ms));

// Mock API service
export class MockApiService {
  private static instance: MockApiService;
  private startups: Startup[] = mockApiResponses.startups;
  private user: User = mockApiResponses.user;
  private reports: Report[] = mockApiResponses.reports;
  private uploadFiles: UploadFile[] = mockApiResponses.uploadFiles;

  static getInstance(): MockApiService {
    if (!MockApiService.instance) {
      MockApiService.instance = new MockApiService();
    }
    return MockApiService.instance;
  }

  // Auth endpoints
  async login(email: string, password: string): Promise<APIResponse<{ user: User; token: string }>> {
    await delay();
    
    if (email === 'analyst@easemytrip.com' && password === 'password') {
      return {
        data: {
          user: this.user,
          token: 'mock-jwt-token-' + Date.now(),
        },
        success: true,
        timestamp: new Date(),
      };
    }
    
    throw new Error('Invalid credentials');
  }

  async logout(): Promise<APIResponse<null>> {
    await delay(200);
    return {
      data: null,
      success: true,
      timestamp: new Date(),
    };
  }

  async refreshToken(): Promise<APIResponse<{ token: string }>> {
    await delay(300);
    return {
      data: {
        token: 'mock-jwt-token-refreshed-' + Date.now(),
      },
      success: true,
      timestamp: new Date(),
    };
  }

  // Startups endpoints
  async getStartups(params?: {
    page?: number;
    limit?: number;
    search?: string;
    filters?: any;
  }): Promise<PaginatedResponse<Startup>> {
    await delay();
    
    const { page = 1, limit = 12, search = '', filters = {} } = params || {};
    let filteredStartups = [...this.startups];

    // Apply search filter
    if (search) {
      filteredStartups = filteredStartups.filter(startup =>
        startup.name.toLowerCase().includes(search.toLowerCase()) ||
        startup.sector.toLowerCase().includes(search.toLowerCase())
      );
    }

    // Apply other filters
    if (filters.recommendation?.length) {
      filteredStartups = filteredStartups.filter(startup =>
        filters.recommendation.includes(startup.recommendation)
      );
    }

    if (filters.riskLevel?.length) {
      filteredStartups = filteredStartups.filter(startup =>
        filters.riskLevel.includes(startup.riskLevel)
      );
    }

    if (filters.sector?.length) {
      filteredStartups = filteredStartups.filter(startup =>
        filters.sector.includes(startup.sector)
      );
    }

    // Pagination
    const startIndex = (page - 1) * limit;
    const endIndex = startIndex + limit;
    const paginatedData = filteredStartups.slice(startIndex, endIndex);

    return {
      data: paginatedData,
      pagination: {
        page,
        limit,
        total: filteredStartups.length,
        totalPages: Math.ceil(filteredStartups.length / limit),
      },
    };
  }

  async getStartup(id: string): Promise<APIResponse<Startup>> {
    await delay();
    
    const startup = this.startups.find(s => s.id === id);
    if (!startup) {
      throw new Error('Startup not found');
    }

    return {
      data: startup,
      success: true,
      timestamp: new Date(),
    };
  }

  async updateStartup(id: string, updates: Partial<Startup>): Promise<APIResponse<Startup>> {
    await delay();
    
    const index = this.startups.findIndex(s => s.id === id);
    if (index === -1) {
      throw new Error('Startup not found');
    }

    this.startups[index] = { ...this.startups[index], ...updates };
    
    return {
      data: this.startups[index],
      success: true,
      timestamp: new Date(),
    };
  }

  async deleteStartup(id: string): Promise<APIResponse<null>> {
    await delay();
    
    const index = this.startups.findIndex(s => s.id === id);
    if (index === -1) {
      throw new Error('Startup not found');
    }

    this.startups.splice(index, 1);
    
    return {
      data: null,
      success: true,
      timestamp: new Date(),
    };
  }

  // Analysis endpoints
  async triggerAnalysis(startupId: string): Promise<APIResponse<{ analysisId: string }>> {
    await delay(1000);
    
    const analysisId = `analysis-${startupId}-${Date.now()}`;
    
    return {
      data: { analysisId },
      success: true,
      timestamp: new Date(),
    };
  }

  async getAnalysis(startupId: string): Promise<APIResponse<Analysis>> {
    await delay();
    
    const analysis = generateMockAnalysis(startupId);
    
    return {
      data: analysis,
      success: true,
      timestamp: new Date(),
    };
  }

  // Reports endpoints
  async getReports(): Promise<APIResponse<Report[]>> {
    await delay();
    
    return {
      data: this.reports,
      success: true,
      timestamp: new Date(),
    };
  }

  async generateReport(startupId: string, templateId: string, settings: any): Promise<APIResponse<Report>> {
    await delay(2000);
    
    const report: Report = {
      id: `report-${Date.now()}`,
      startupId,
      templateId,
      title: `Investment Analysis Report - ${Date.now()}`,
      status: 'ready',
      format: 'pdf',
      generatedAt: new Date(),
      downloadUrl: `https://example.com/reports/report-${Date.now()}.pdf`,
      settings,
    };

    this.reports.unshift(report);
    
    return {
      data: report,
      success: true,
      timestamp: new Date(),
    };
  }

  // Upload endpoints
  async uploadFile(file: File): Promise<APIResponse<UploadFile>> {
    await delay(1500);
    
    const uploadFile: UploadFile = {
      id: `file-${Date.now()}`,
      name: file.name,
      size: file.size,
      type: file.type,
      status: 'completed',
      progress: 100,
      uploadedAt: new Date(),
      processedAt: new Date(),
      url: `https://example.com/files/${file.name}`,
      metadata: {
        pages: Math.floor(Math.random() * 50) + 1,
        extractedText: 'Sample extracted text content...',
        thumbnailUrl: `https://via.placeholder.com/150x200?text=${file.name.charAt(0)}`,
      },
    };

    this.uploadFiles.unshift(uploadFile);
    
    return {
      data: uploadFile,
      success: true,
      timestamp: new Date(),
    };
  }

  async getUploadFiles(): Promise<APIResponse<UploadFile[]>> {
    await delay();
    
    return {
      data: this.uploadFiles,
      success: true,
      timestamp: new Date(),
    };
  }

  // User endpoints
  async getProfile(): Promise<APIResponse<User>> {
    await delay();
    
    return {
      data: this.user,
      success: true,
      timestamp: new Date(),
    };
  }

  async updateProfile(updates: Partial<User>): Promise<APIResponse<User>> {
    await delay();
    
    this.user = { ...this.user, ...updates };
    
    return {
      data: this.user,
      success: true,
      timestamp: new Date(),
    };
  }
}

export const mockApi = MockApiService.getInstance();
