import { Box, Typography, Card, CardContent, Chip } from '@mui/material';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { ChartContainer } from './ChartContainer';

const riskTrends = [
  { month: 'Jan', financial: 25, market: 35, operational: 20 },
  { month: 'Feb', financial: 22, market: 32, operational: 18 },
  { month: 'Mar', financial: 28, market: 38, operational: 22 },
  { month: 'Apr', financial: 20, market: 30, operational: 15 },
  { month: 'May', financial: 18, market: 28, operational: 12 },
  { month: 'Jun', financial: 15, market: 25, operational: 10 },
];

const riskMitigation = [
  { action: 'Diversify Revenue', status: 'Completed', effectiveness: 85 },
  { action: 'Increase Cash Reserves', status: 'In Progress', effectiveness: 70 },
  { action: 'Hire Key Roles', status: 'Planned', effectiveness: 60 },
];

const getStatusColor = (status: string): 'success' | 'warning' | 'info' | 'default' => {
  switch (status) {
    case 'Completed': return 'success';
    case 'In Progress': return 'warning';
    case 'Planned': return 'info';
    default: return 'default';
  }
};

export const RiskVisualization: React.FC = () => {
  return (
    <Box>
      <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(400px, 1fr))', gap: 3 }}>
        <ChartContainer title="Risk Trends" subtitle="Risk levels across categories over time">
          <ResponsiveContainer width="100%" height="100%">
            <AreaChart data={riskTrends}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="month" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Area type="monotone" dataKey="financial" stackId="1" stroke="#ff4444" fill="#ff4444" fillOpacity={0.6} name="Financial Risk" />
              <Area type="monotone" dataKey="market" stackId="1" stroke="#ffaa00" fill="#ffaa00" fillOpacity={0.6} name="Market Risk" />
              <Area type="monotone" dataKey="operational" stackId="1" stroke="#44aaff" fill="#44aaff" fillOpacity={0.6} name="Operational Risk" />
            </AreaChart>
          </ResponsiveContainer>
        </ChartContainer>

        <Card>
          <CardContent>
            <Typography variant="h6" fontWeight="bold" gutterBottom>
              Risk Mitigation Actions
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
              Current mitigation strategies and their effectiveness
            </Typography>
            
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              {riskMitigation.map((action, index) => (
                <Box key={index} sx={{ p: 2, border: '1px solid', borderColor: 'divider', borderRadius: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Box>
                    <Typography variant="body1" fontWeight="500">
                      {action.action}
                    </Typography>
                    <Box sx={{ display: 'flex', gap: 1, mt: 1 }}>
                      <Chip size="small" label={action.status} color={getStatusColor(action.status)} />
                      <Chip size="small" label={`${action.effectiveness}% effective`} variant="outlined" />
                    </Box>
                  </Box>
                </Box>
              ))}
            </Box>
          </CardContent>
        </Card>
      </Box>
    </Box>
  );
};
