import { Startup, Analysis, User, Report, UploadFile } from '@/types';

// Mock data generators
export const generateMockStartups = (count: number = 20): Startup[] => {
  const sectors = ['FinTech', 'HealthTech', 'EdTech', 'E-commerce', 'SaaS', 'AI/ML', 'IoT', 'CleanTech'];
  const stages = ['Pre-Seed', 'Seed', 'Series A', 'Series B', 'Series C'];
  const recommendations = ['Strong Buy', 'Buy', 'Hold', 'Sell', 'Strong Sell'] as const;
  const riskLevels = ['Low', 'Medium', 'High', 'Critical'] as const;
  const statuses = ['completed', 'processing', 'pending', 'failed'] as const;

  const companyNames = [
    'TechFlow Solutions', 'InnovateLab', 'DataDrive Inc', 'CloudSync Pro', 'SmartFinance',
    'HealthBridge', 'EduNext', 'RetailGenius', 'GreenEnergy Co', 'AIVision',
    'CyberShield', 'MobileFirst', 'BlockchainPro', 'VirtualReality Inc', 'RoboTech',
    'BioInnovate', 'SpaceTech', 'AgriSmart', 'LogisticsPro', 'MediaStream'
  ];

  return Array.from({ length: count }, (_, index) => ({
    id: `startup-${index + 1}`,
    name: companyNames[index % companyNames.length],
    industry: sectors[Math.floor(Math.random() * sectors.length)],
    sector: sectors[Math.floor(Math.random() * sectors.length)],
    stage: stages[Math.floor(Math.random() * stages.length)],
    overallScore: Math.floor(Math.random() * 100),
    aiScore: Math.floor(Math.random() * 100),
    recommendation: recommendations[Math.floor(Math.random() * recommendations.length)],
    riskLevel: riskLevels[Math.floor(Math.random() * riskLevels.length)],
    lastUpdated: new Date(Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000),
    status: statuses[Math.floor(Math.random() * statuses.length)],
    logoUrl: `https://via.placeholder.com/64x64?text=${companyNames[index % companyNames.length].charAt(0)}`,
    description: `Innovative ${sectors[Math.floor(Math.random() * sectors.length)]} company focused on disrupting traditional markets.`,
    foundedYear: 2018 + Math.floor(Math.random() * 6),
    location: ['Mumbai', 'Bangalore', 'Delhi', 'Pune', 'Hyderabad'][Math.floor(Math.random() * 5)],
    website: `https://${companyNames[index % companyNames.length].toLowerCase().replace(/\s+/g, '')}.com`,
    funding: `$${(Math.random() * 10 + 0.5).toFixed(1)}M`,
    valuation: `$${(Math.random() * 50 + 5).toFixed(1)}M`,
    analysisProgress: Math.floor(Math.random() * 100),
    metrics: {
      revenue: Math.floor(Math.random() * 10000000),
      burnRate: Math.floor(Math.random() * 500000),
      runway: Math.floor(Math.random() * 24),
      userGrowth: Math.floor(Math.random() * 200),
      marketSize: Math.floor(Math.random() * 1000000000),
      teamSize: Math.floor(Math.random() * 100) + 5,
      fundingRaised: Math.floor(Math.random() * 50000000),
    },
  }));
};

