export const generateMockReports = (count: number = 10): Report[] => {
  const formats = ['pdf', 'word', 'powerpoint'] as const;
  const statuses = ['ready', 'generating', 'failed'] as const;

  return Array.from({ length: count }, (_, index) => ({
    id: `report-${index + 1}`,
    startupId: `startup-${index + 1}`,
    templateId: 'template-1',
    title: `Investment Analysis Report - Company ${index + 1}`,
    status: statuses[Math.floor(Math.random() * statuses.length)],
    format: formats[Math.floor(Math.random() * formats.length)],
    generatedAt: new Date(Date.now() - Math.random() * 7 * 24 * 60 * 60 * 1000),
    downloadUrl: `https://example.com/reports/report-${index + 1}.pdf`,
    shareUrl: `https://example.com/shared/report-${index + 1}`,
    settings: {
      includeCharts: true,
      includeRawData: false,
      includeRecommendations: true,
      customSections: ['Executive Summary', 'Financial Analysis', 'Risk Assessment'],
      branding: {
        logo: 'https://via.placeholder.com/100x40?text=Logo',
        primaryColor: '#1976d2',
        secondaryColor: '#f50057',
        fontFamily: 'Roboto',
      },
    },
  }));
};

export const generateMockUploadFiles = (count: number = 5): UploadFile[] => {
  const fileTypes = ['application/pdf', 'application/vnd.ms-powerpoint', 'audio/mpeg', 'video/mp4'];
  const statuses = ['completed', 'processing', 'pending', 'failed'] as const;

  return Array.from({ length: count }, (_, index) => ({
    id: `file-${index + 1}`,
    name: `document-${index + 1}.pdf`,
    size: Math.floor(Math.random() * 10000000) + 100000,
    type: fileTypes[Math.floor(Math.random() * fileTypes.length)],
    status: statuses[Math.floor(Math.random() * statuses.length)],
    progress: Math.floor(Math.random() * 100),
    uploadedAt: new Date(Date.now() - Math.random() * 24 * 60 * 60 * 1000),
    processedAt: Math.random() > 0.5 ? new Date() : undefined,
    url: `https://example.com/files/file-${index + 1}`,
    metadata: {
      pages: Math.floor(Math.random() * 50) + 1,
      extractedText: 'Sample extracted text content...',
      thumbnailUrl: `https://via.placeholder.com/150x200?text=Doc${index + 1}`,
    },
  }));
};

