import {
  Box,
  Card,
  CardContent,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Chip,
  Avatar,
  Divider,
} from '@mui/material';
import {
  Description,
  PictureAsPdf,
  TableChart,
  Slideshow,
  Download,
  Delete,
  Visibility,
  Schedule,
} from '@mui/icons-material';

interface UploadHistoryItem {
  id: string;
  name: string;
  type: string;
  size: number;
  uploadDate: Date;
  status: 'completed' | 'processing' | 'failed';
  startupName?: string;
}

const mockHistory: UploadHistoryItem[] = [
  {
    id: '1',
    name: 'Financial_Report_Q3.pdf',
    type: 'application/pdf',
    size: 2.4 * 1024 * 1024,
    uploadDate: new Date(Date.now() - 2 * 60 * 60 * 1000),
    status: 'completed',
    startupName: 'TechFlow Solutions',
  },
  {
    id: '2',
    name: 'Business_Plan_2024.docx',
    type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
    size: 1.8 * 1024 * 1024,
    uploadDate: new Date(Date.now() - 5 * 60 * 60 * 1000),
    status: 'processing',
    startupName: 'HealthBridge',
  },
  {
    id: '3',
    name: 'Pitch_Deck.pptx',
    type: 'application/vnd.openxmlformats-officedocument.presentationml.presentation',
    size: 5.2 * 1024 * 1024,
    uploadDate: new Date(Date.now() - 24 * 60 * 60 * 1000),
    status: 'completed',
    startupName: 'EduNext',
  },
  {
    id: '4',
    name: 'Revenue_Data.xlsx',
    type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    size: 890 * 1024,
    uploadDate: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000),
    status: 'failed',
    startupName: 'GreenEnergy Co',
  },
];

export const UploadHistory: React.FC = () => {
  const getFileIcon = (type: string) => {
    if (type.includes('pdf')) return <PictureAsPdf color="error" />;
    if (type.includes('sheet') || type.includes('excel')) return <TableChart color="success" />;
    if (type.includes('presentation') || type.includes('powerpoint')) return <Slideshow color="warning" />;
    return <Description color="primary" />;
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'success';
      case 'processing': return 'warning';
      case 'failed': return 'error';
      default: return 'default';
    }
  };

  const formatFileSize = (bytes: number) => {
    const mb = bytes / (1024 * 1024);
    return mb < 1 ? `${(bytes / 1024).toFixed(1)} KB` : `${mb.toFixed(1)} MB`;
  };

  const formatDate = (date: Date) => {
    const now = new Date();
    const diffHours = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60));
    
    if (diffHours < 1) return 'Just now';
    if (diffHours < 24) return `${diffHours}h ago`;
    const diffDays = Math.floor(diffHours / 24);
    return `${diffDays}d ago`;
  };

  const completedCount = mockHistory.filter(item => item.status === 'completed').length;
  const processingCount = mockHistory.filter(item => item.status === 'processing').length;
  const failedCount = mockHistory.filter(item => item.status === 'failed').length;

  return (
    <Card>
      <CardContent>
        <Typography variant="h6" gutterBottom>
          Upload History
        </Typography>

        {/* Summary Stats */}
        <Box sx={{ display: 'flex', gap: 1, mb: 3 }}>
          <Chip 
            label={`${completedCount} completed`}
            size="small" 
            color="success" 
          />
          <Chip 
            label={`${processingCount} processing`}
            size="small" 
            color="warning" 
          />
          {failedCount > 0 && (
            <Chip 
              label={`${failedCount} failed`}
              size="small" 
              color="error" 
            />
          )}
        </Box>

        <List sx={{ maxHeight: 400, overflow: 'auto' }}>
          {mockHistory.map((item, index) => (
            <Box key={item.id}>
              <ListItem sx={{ px: 0 }}>
                <ListItemIcon>
                  <Avatar sx={{ bgcolor: 'transparent', width: 32, height: 32 }}>
                    {getFileIcon(item.type)}
                  </Avatar>
                </ListItemIcon>

                <ListItemText
                  primary={
                    <Typography variant="body2" noWrap>
                      {item.name}
                    </Typography>
                  }
                  secondary={
                    <Box>
                      <Typography variant="caption" color="text.secondary">
                        {formatFileSize(item.size)}  {formatDate(item.uploadDate)}
                      </Typography>
                      {item.startupName && (
                        <Typography variant="caption" color="primary" sx={{ display: 'block' }}>
                          {item.startupName}
                        </Typography>
                      )}
                      <Chip
                        size="small"
                        label={item.status}
                        color={getStatusColor(item.status) as any}
                        sx={{ mt: 0.5 }}
                        icon={item.status === 'processing' ? <Schedule /> : undefined}
                      />
                    </Box>
                  }
                />

                <ListItemSecondaryAction>
                  <Box sx={{ display: 'flex', flexDirection: 'column', gap: 0.5 }}>
                    {item.status === 'completed' && (
                      <>
                        <IconButton size="small" title="View">
                          <Visibility fontSize="small" />
                        </IconButton>
                        <IconButton size="small" title="Download">
                          <Download fontSize="small" />
                        </IconButton>
                      </>
                    )}
                    <IconButton size="small" title="Delete">
                      <Delete fontSize="small" />
                    </IconButton>
                  </Box>
                </ListItemSecondaryAction>
              </ListItem>
              
              {index < mockHistory.length - 1 && <Divider />}
            </Box>
          ))}
        </List>

        {mockHistory.length === 0 && (
          <Box sx={{ textAlign: 'center', py: 4 }}>
            <Typography variant="body2" color="text.secondary">
              No upload history
            </Typography>
          </Box>
        )}
      </CardContent>
    </Card>
  );
};
